import React from 'react'
import { Link } from 'react-router-dom';

import { sourceCodeList } from "../config/constant";
export default function Home() {

    return (
        <div className='container my-2'>
            <h2 className='text-center'>Component</h2>
            <div className="row row-cols-1 row-cols-md-2 g-4">
                {sourceCodeList.map((element, index) => {
                    return (
                        <div key={index}>
                            <div className="card text-center border-dark h-100">
                                <h5 className="card-header">{element.component_name}</h5>
                                <div className="card-body">
                                    <p className="card-text">{element.description}</p>
                                    <Link to={element.path} className="btn btn-primary">Read More</Link>
                                </div>
                            </div>
                        </div>
                    )
                })}
            </div>
        </div>
    )
}
