import { Link, useLocation } from "react-router-dom";
import { sourceCodeList } from '../config/constant'
export default function SideBar() {
    let location = useLocation();

    return (
        <>
            <div className="p-2 text-white fixed sideColor" style={{
                width: "240px",
                height: "100%",
                position: " fixed",
                zIndex: "1",
                overflowX: "hidden",
            }}>

                <ul className="nav nav-pills flex-column mb-auto mx-2" style={{
                    paddingBottom: "100px"
                }}>
                    {sourceCodeList.map((element, index) => {
                        return (<li className="nav-item mb-3" >
                            <Link key={index} to={element.path} className={`nav-link ${location.pathname === element.path ? 'active bg-dark' : "text-white"}`} >
                                {element.component_name}
                            </Link>
                        </li>)
                    })}
                </ul>


            </div>
        </>
    )
}